// contact form component

.contacts {
    &_header {
        &-title {
            margin-bottom: 20px;
        }
    }

    &_form {
        .field, .btn {
            width: 100%;
        }

        .btn {
            margin-top: 20px;
        }

        .field {
            &.textarea {
                height: 150px;
                padding: 20px;
            }

            &-wrapper {
                position: relative;
                margin-bottom: 15px;

                .field {
                    padding-right: 60px;
                }

                .label {
                    position: absolute;
                    right: 20px;
                    top: 50%;
                    transform: translateY(-50%);
                    color: $textSecondary;
                }
            }
        }
    }
}

// media queries

@media screen and (min-width: 575.98px) {
    .contacts {
        &_form {
            .btn {
                margin-top: 40px;
                width: fit-content;
            }
            .field-wrapper {
                width: calc(50% - 15px);
                margin-bottom: 30px;
            }
        }
    }
}