// review section

.review {
    margin-top: 60px;

    &_header {
        margin-bottom: 20px;
    }

    &_rating {
        margin: 0 -10px 30px;

        &-block {
            width: 50%;
            padding: 0 10px;
            margin-bottom: 15px;

            &:last-of-type, &[data-order="3"] {
                margin-bottom: 0;
            }

            .label {
                color: $textSecondary;
                @include textSm;
            }
        }
    }
}

// media queries

@media screen and (min-width: 575.98px) {
    .review {
        &_rating {
            margin: 0 -15px 30px;

            &-block {
                padding: 0 15px;

                &:last-of-type, &[data-order="3"] {
                    margin-bottom: 0;
                }

                .label {
                    color: $textSecondary;
                    @include textSm;
                }
            }
        }
    }
}

@media screen and (min-width: 991.98px) {
    .review {
        margin-top: 80px;

        &_header {
            margin-bottom: 30px;
        }
    }
}