// page header component

.page {
    padding: 60px 0;
    background: $backgroundHighlight;
    .breadcrumbs {
        margin-bottom: 10px;
        .list-item {
            margin-right: 10px;
            &:last-of-type {
                margin-right: 0;
                .link {
                    padding-right: 0;
                    pointer-events: none;
                    font-weight: 400;
                    color: $textColor;
                    &:after {
                        display: none;
                    }
                }
            }
            .link {
                padding-right: 10px;
                position: relative;
                color: $accent;
                font-weight: 600;
                &:after {
                    content: '/';
                    position: absolute;
                    right: -3px;
                    color: $textColor;
                }
            }
        }
    }
}

